<template>
    <v-container>
        <v-card>
            <v-card-title>History</v-card-title>
            <v-data-table :items="history"
                          :headers="headers"
                          dense
                          hide-default-footer
                          class="elevation-1">
                <template v-slot:[`item.createdOn`]="{ item }">

                    {{ new Date(item.createdOn).toLocaleString()  }}

                </template>
            </v-data-table>
        </v-card>

    </v-container>
</template>

<script>
    import axios from '../plugins/axios'
    export default {
        name: 'CalculatorComponent ',
        data() {
            return {
                history: [],
                headers: [

                    { title: 'Calculation', value: 'computing' },
                    { title: 'Date', value: 'createdOn' },

                ]
            };
        },
 
        methods: {
            getHistory() {
                axios.get('/home',).then(response => {
                     this.history = response.data;
                }).catch(error => {
                     this.sendError(error,'getHistory');

                });
            },

            sendError(error,methodName) {
                let errorObject = {
                    message: error,
                    place: 'HistoryComponent',
                    method: methodName
                };
                axios.post('/home/Error', errorObject);
             },
        },

          mounted() {
             this.getHistory(); 
        }
       
    };
</script>

<style scoped>
    .v-card {
        margin: 16px;
    }

    .v-text-field {
        font-size: 2em;
    }

    .v-btn {
        margin: 4px;
    }
</style>
